.course-content-wrapp {
  display: flex;
  flex-direction: column;
}

.video-player-wrapp {
  @include center;
  margin-bottom: 10px;
  width: 100%;
  height: 200px;
  background-color: $black-bg-color;
  border-radius: 10px;

  @include mq(desk) {
    height: 400px;
  }
}

